@use postcss-preset-env!important;

* {
  box-sizing: inherit!important;
}

html {
  box-sizing: border-box!important;
  height: 100%!important;
}

body {
  background-color: #131415!important;
  color: #fff!important;
  font-family: sans-serif!important;
  font-style: 1rem!important;
  line-height: 1.5!important;
  margin: 0 auto!important;
  min-height: 100vh!important;
  width: 100%!important;
}

figure {
  margin: 0!important;
}

img {
  height: auto!important;
  max-width: 100%!important;
  vertical-align: middle!important;
}

input {
  border: 0!important;
  font: inherit!important;
  margin: 0!important;
  padding: 0!important;
}

.search {
  font-size: 1.5rem!important;
  margin: 0.2em 0.2em 0.5em!important;
  max-width: 92%!important;
  padding: 1% !important;
  background-color: #c5c4c2 !important;
}

.search [type='search'] {
  background-color: #131b20!important;
  color: #fff!important;
  padding: 0.2em 0.2em 0.5em !important;
  text-align: center!important;
  width: 100%!important;
}

.movies {
  display: grid!important;
  grid-gap: 3em 2em!important;
  grid-template-columns: repeat(auto-fill, minmax(12em, 1fr))!important;
  list-style: none!important;
  margin: 0!important;
  padding: 0!important;
}

.movie__figure {
  position: relative!important;
}

.movie__poster {
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.5)!important;
  margin-bottom: 0.5em!important;
}

.movie__vote {
  align-items: center!important;
  background-color: #f68d01!important;
  border-radius: 50%!important;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.25)!important;
  display: flex!important;
  height: 2.25em!important;
  justify-content: center!important;
  padding: 0.5em!important;
  position: absolute!important;
  color:black;
  right: 0!important;
  text-align: center!important;
  top: 75%!important;
  transform: translateX(50%)!important;
  width: 2.25em!important;
}

.movie__title {
  font-size: 1.199rem!important;
  font-weight: 400!important;
  margin: 0!important;
  text-align: center;
  color: white;
}

.app {
    width: 90%!important;
    margin: 0 auto;
}

.input-group-append {
    padding: 0.3em 0.2em 0.6em !important;
}

.blueberrynav{
  background-color: #c5c4c2 !important;
}



/* Table responsive begin */

@media screen and (max-width: 575px) {
  .table-xs-responsive th, .table-xs-responsive td {
      width: 100% !important;
      display: block !important;
      box-sizing:border-box;
  }
  }
  
  @media screen and (max-width: 767px) {
  .table.table-sm-responsive th, .table.table-sm-responsive td {
      width: 100% !important;
      display: block !important;
      box-sizing:border-box;
  }
  }
  
  @media screen and (max-width: 992px) {
  .table.table-sm-responsive th, .table.table-sm-responsive td {
      width: 100% !important;
      display: block !important;
      box-sizing:border-box;
  }
  }
  
  @media screen and (max-width: 1200px) {
  .table.table-sm-responsive th, .table.table-sm-responsive td {
      width: 100% !important;
      display: block !important;
      box-sizing:border-box;
  }
  }

.jumbotron{
  background-image: url('https://i.pinimg.com/originals/e0/46/79/e04679c79c994d61363d9b1ba888e246.jpg');
  background-size: cover;
  height: 100%;
}

.movie:hover {
  padding: 10px;
}